@font-face {
    font-family: fontRandom;
    src: url("./RobotoMono-Regular.ttf");
  }
  
  @import url("https://fonts.googleapis.com/css2?family=Baloo+2&display=swap");
  
  
  body{
    padding: 2rem 0rem;
    
  }
  
  .imageen{
    width: 100%;
  
  
  }
  
  .div-central {
      width: 100%;
      height: 100%;
      box-shadow: 0 0 1rem 0 rgba(255, 255, 255, 0.322); 
      border-radius: 10px;
      position: relative;
      z-index: 1;
      overflow: auto;
  }
  
  .background-page{
      
      background-size: cover;
      width: 100%;
      position: fixed;
      height: 100vh;
      filter: blur(5px);
  }
    
  .div-central:before {
      content: "";
      position: absolute;
      background: inherit;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      box-shadow: inset 0 0 2000px rgba(22, 55, 145, 0.5);
      filter: blur(7px);
  }
  
  .contenedor-mov{
    width: 100%;
    margin: 40px auto;
    padding: 20px;
  
  }
  
  .elemento {
    width: 150px;
    height: 150px;
  
    transition-property: border-radius, transform;
    
    transition-duration: 1s;
    transition-timing-function: ease-in-out;;
    transition-delay: 0s;
  }
  
  .contenedor-mov:hover .elemento{
    border-radius: 50%;
    transform: translateX(25px);
  }
  
  .div-aclaraciones {
    padding-top: 9%;
  }
  
  .card-indentify{
    padding: 20px;
  }
  
  .div-master{
      padding-top: 4%;
      color: #ffffff;
  }
  
  .dropdown-menu li {
      position: relative;
    }
    .dropdown-menu  .dropdown-submenu {
      display:none;
      position: absolute;
      left: 100%;
      top: -7px;
    }
    .dropdown-menu .dropdown-submenu-left {
      right: 100%;
      left: auto;
    }
    .dropdown-menu > li:hover > .dropdown-submenu {
      display:block;
    }
  
