  .button {
      display: inline-block;
      text-align: center;
      vertical-align: middle;
      padding: 9px 21px;
      border: 0px solid #83d688;
      border-radius: 22px;
      background: #91b574;
      background: -webkit-gradient(linear, left top, left bottom, from(#91b574), to(#9dc75e));
      background: -moz-linear-gradient(top, #91b574, #9dc75e);
      background: linear-gradient(to bottom, #91b574, #9dc75e);
      font: normal normal bold 20px arial;
      color: #ffffff;
      text-decoration: none;
  }
  .button:hover,
  .button:focus {
      border: 0px solid #97f69c;
      background: #aed98b;
      background: -webkit-gradient(linear, left top, left bottom, from(#aed98b), to(#bcef71));
      background: -moz-linear-gradient(top, #aed98b, #bcef71);
      background: linear-gradient(to bottom, #aed98b, #bcef71);
      color: #ffffff;
      text-decoration: none;
  }
  .button:active {
      background: #576d46;
      background: -webkit-gradient(linear, left top, left bottom, from(#576d46), to(#9dc75e));
      background: -moz-linear-gradient(top, #576d46, #9dc75e);
      background: linear-gradient(to bottom, #576d46, #9dc75e);
  }
  
  .zoom{
    transition: transform .3s;
  }
  .zoom:hover{
    transform: scale(1.5);
  }
  
  #infoDevops{
    text-align: center;
  }
  